.controls {
    display: flex;
    justify-content: center;
    margin-top: 16px;
}

.button {
    display: inline-block;
    min-width: 240px;
    border: 0;
    padding: 8px 16px;
    margin-left: 4px;
    margin-right: 4px;
    border-radius: 3px;
    background-color: #3884ff;
    transition: all 200ms ease;
    font-family: inherit;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    font-style: normal;
    text-align: center;
    text-decoration: none;
    color: #fff;
    cursor: pointer;
}

.disabled {
    composes: button;
    pointer-events: none;
    background-color: #bdbdbd;
}

.button:hover,
.button:focus {
    background-color: #1f65d6;
}